.about{
  padding-bottom: 75px;
  &__grid{
    display: grid;
    grid-template-columns: 100%;
    gap: 42px;
  }
}


@media screen and (max-width: 992px){
  .about{
    padding-bottom: 40px;
    &__grid{
      gap: 24px;
    }
  }
}


@media screen and (max-width: 767px){
  .about{
    &__grid{
      gap: 16px;
    }
  }
}
