.catalog{
  padding-bottom: 75px;
  &__grid{
    display: grid;
    grid-template-columns: repeat(3,1fr);
    gap: 30px 57px;
  }
}


@media screen and (max-width: 992px){
  .catalog{
    &__grid{
      gap: 24px 45px;
    }
  }
}

@media screen and (max-width: 767px){
  .catalog{
    &__grid{
      grid-template-columns: 1fr 1fr;
    }
  }
}


@media screen and (max-width: 576px){
  .catalog{
    &__grid{
      grid-template-columns: 1fr;
      gap: 24px;
    }
  }
}
