.policy{
  padding-bottom: 75px;
  &__title{
    margin-top: 8px;
    font-weight: 500;
    font-size: 14px;
    line-height: 19px;
    letter-spacing: 0.05em;
    text-transform: uppercase;
  }
  &__grid{
    margin-top: 28px;
    display: grid;
    grid-template-columns: 100%;
    gap: 28px;
  }
  &__row{
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 40px;
    background: #F4F4F4;
    padding: 40px;
    font-size: 14px;
    line-height: 19px;
    &--full{
      display: block;
    }
    p,
    ul,
    h3{
      font-size: inherit;
      line-height: inherit;
      &:not(:last-child){
        margin-bottom: 20px;
      }
    }
    b,
    h3{
      font-weight: 500;
    }
    ul{
      padding-left: 9px;
    }
    li{
      line-height: inherit;
      display: flex;
      &:before{
        content: '';
        width: 3px;
        height: 3px;
        display: block;
        border-radius: 50%;
        background-color: $black;
        margin-right: 10px;
        transform: translateY(9px);
        flex-shrink: 0;
      }
    }
    a{
      text-decoration: underline;
    }
  }
  &__col{
    line-height: inherit;
  }
}


@media screen and (max-width: 992px){
  .policy{
    padding-bottom: 40px;
    &__title{
      margin-top: 24px;
    }
    &__grid{
      margin-top: 24px;
      gap: 24px;
    }
    &__row{
      grid-template-columns: 100%;
    }
  }
}


@media screen and (max-width: 767px){
  .policy{
    &__title{
      margin-top: 16px;
    }
    &__grid{
      margin-top: 16px;
      gap: 16px;
    }
    &__row{
      padding: 16px;
      gap: 36px;
      p,
      ul,
      h3{
        line-height: inherit;
        &:not(:last-child){
          margin-bottom: 15px;
        }
      }
    }
  }
}
