.header{
  top: 0;
  width: 100%;
  position: sticky;
  z-index: 1000;
  background-color: #fff;
  &__top{
    border-bottom: 1px solid #E5E5E5;
    &-inner{
      padding: 16px 0 12px;
      display: flex;
      align-items: center;
      justify-content: center;
      position: relative;
    }
  }
  &__logo{
    display: block;
    width: 193px;
    img{
      width: 100%;
      display: block;
    }
  }
  &__slogan{
    position: absolute;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
    width: 267.68px;
    img{
      width: 100%;
    }
  }
}

.burger{
  display: none;
  width: 24px;
  height: 24px;
  background-image: url(../img/burger.svg);
  background-repeat: no-repeat;
  background-position: center;
}

.nav{
  padding: 23px 0 18px;
  border-bottom: 1px solid #E5E5E5;
  &__inner{
    display: flex;
    align-items: center;
    justify-content: center;
  }
  &__link{
    margin: 0 20px;
    font-size: 14px;
    line-height: 19px;
    letter-spacing: 0.05em;
    text-transform: uppercase;
    &:hover{
      color: #828282;
    }
    &._active{
      font-weight: 500;
    }
    &--hide{
      display: none;
    }
  }
}


@media screen and (max-width: 992px){
  .header{
    &__slogan{
      display: none;
    }
    &__top{
      &-inner{
        padding: 24px 0 20px;
      }
    }
    &._open{
      .nav{
        transform: translateX(0);
      }
      .burger{
        background-image: url(../img/burger2.svg);
      }
      .header__logo{
        opacity: 0;
      }
    }
  }

  .burger{
    display: block;
    position: absolute;
    top: 20px;
    left: 0;
  }

  .nav{
    padding: 0 0 40px;
    overflow-y: auto;
    transform: translateX(-100%);
    transition: all .5s;
    position: fixed;
    top: 80px;
    left: 0;
    height: calc(100vh - 80px);
    width: 100%;
    background-color: #fff;
    border: none;
    z-index: 20;
    .container{
      padding: 0;
    }
    &__inner{
      display: block;
    }
    &__link{
      margin: 0;
      border-bottom: 1px solid #E5E5E5;
      height: 51px;
      display: flex;
      align-items: center;
      padding: 0 24px;
      &--hide{
        display: flex;
      }
    }
  }
}


@media screen and (max-width: 767px){
  .header{
    &__top{
      &-inner{
        padding: 16px 0;
      }
    }
    &__logo{
      width: 130px;
    }
  }

  .nav{
    top: 67px;
    height: calc(100vh - 67px);
    &__link{
      padding: 0 16px;
    }
  }
}
