@import 'vars';
@import 'components/_text.scss';
@import 'components/_buttons.scss';
@import "components/_inputs.scss";

.spoiler{
  padding: 0 22.5px 0 16px;
  background: #F4F4F4;
  &._active{
    .spoiler__show{
      img{
        transform: rotate(180deg);
      }
    }
  }
  &__show{
    padding: 16px 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    cursor: pointer;
    img{
      transition: all .4s;
    }
  }
  &__title{
    font-weight: 500;
    font-size: 14px;
    line-height: 19px;
    padding-right: 10px;
  }
  &__hidden{
    padding: 4px 0 16px;
    display: none;
  }
  &__links{
    display: grid;
    grid-template-columns: 100%;
    gap: 5px;
  }
  &__link{
    font-size: 14px;
    line-height: 19px;
    text-decoration-line: underline;
  }
}

.swiper{
  &-pagination{
    display: flex;
    align-items: center;
    justify-content: center;
    &-bullet{
      background: #E5E5E5;
      width: 8px;
      height: 8px;
      opacity: 1;
      margin: 0 4px !important;
      &-active{
        background: $black;
      }
    }
  }
}
