.main{
  margin-top: 48px;
  &__banner{
    display: block;
    padding-bottom: 44.118%;
    position: relative;
    overflow: hidden;
    img{
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
      transition: all .3s;
    }
    &:hover{
      img{
        opacity: .8;
      }
    }
  }
  .swiper{
    &-pagination{
      margin-top: 16px;
      position: static;
    }
  }
}

.brands{
  padding: 48px 0 75px;
  &__group{
    display: grid;
    grid-template-columns: 100%;
    gap: 48px;
  }
  &__slogan{
    display: none;
  }
}


@media screen and (max-width: 992px){
  .brands{
    &__slogan{
      display: block;
      width: 80px;
      margin: 40px auto 0;
    }
  }
}


@media screen and (max-width: 767px){
  .main{
    &__banner{
      padding-bottom: 100%;
    }
  }

  .brands{
    padding: 24px 0 40px;
    &__group{
      gap: 24px;
    }
    &__slogan{
      width: 220px;
    }
  }
}
