.contacts{
  padding-bottom: 75px;
  &__grid{
    margin-top: 3px;
    display: grid;
    grid-template-columns: 425px calc(100% - 448px);
    justify-content: space-between;
  }
  &__mob-select{
    display: none;
  }
}

.map{
  height: 706px;
  width: 100%;
  min-height: 100%;
}

.ymaps-2-1-79-balloon__close-button{
  opacity: 1 !important;
  &:hover{
    opacity: .3 !important;
  }
}

.ymaps-2-1-79-balloon__content{
  font-family: $font1 !important;
  .contacts-select__item-title{
    font-weight: 500;
  }
  .contacts-select__item-desc{
    font-weight: 300;
  }
}

.contacts-select{
  padding: 0 22.5px 0 16px;
  background: #F4F4F4;
  &:not(:last-child){
    border-bottom: 1px solid #E5E5E5;
  }
  &._active{
    .contacts-select__show{
      img{
        transform: rotate(180deg);
      }
    }
  }
  &__show{
    padding: 16px 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    cursor: pointer;
    font-weight: 500;
    font-size: 14px;
    line-height: 19px;
    text-transform: uppercase;
    img{
      transition: all .4s;
    }
  }
  &__hidden{
    padding: 4px 0 16px;
    display: none;
  }
  &__items{
    display: grid;
    grid-template-columns: 100%;
    gap: 20px;
  }
  &__item{
    &-title,
    &-desc{
      font-size: 14px;
      line-height: 19px;
    }
    &-title{
      font-weight: 500;
    }
    &-desc{
      p{
        line-height: inherit;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .contacts{
    padding-bottom: 40px;
    &__grid{
      grid-template-columns: 100%;
      gap: 24px;
    }
    &__left{
      position: relative;
    }
    &__mob-select{
      display: block;
      padding: 0 22.5px 0 24px;
      background: #F4F4F4;
      .contacts-select__show{
        img{
          transform: rotate(180deg);
        }
      }
      .contacts-select__hidden{
        display: block !important;
      }
      &._active{
        pointer-events: none;
        &~.contacts__cities{
          opacity: 1;
          pointer-events: unset;
        }
      }
    }
    &__cities{
      transition: all .3s;
      opacity: 0;
      pointer-events: none;
      position: absolute;
      top: 51px;
      left: 0;
      width: 100%;
      z-index: 10;
      .contacts-select{
        padding: 0 22.5px 0 24px;
        background-color: #fff;
        &__hidden{
          display: none !important;
        }
        &__show{
          img{
            display: none;
          }
        }
      }
    }
  }

  .map{
    height: 488px;
    min-height: unset;
  }
}


@media screen and (max-width: 767px){
  .contacts{
    &__grid{
      gap: 16px;
    }
    &__mob-select{
      padding: 0 16px;
    }
    &__cities{
      .contacts-select{
        padding: 0 16px;
      }
    }
  }

  .map{
    height: 760px;
  }
}
