@import "../vars.scss";
.breadcrumbs {
    padding: 16px 0;
    &__item {
        font-size: 12px;
        line-height: 16px;
        display: inline;
            &:not(:first-child) {
            &:before {
                content: '/';
                margin-right: 3px;
            }
        }
    }
    a{
        &:hover{
            text-decoration: underline;
        }
    }
}
