.brand-card{
  display: flex;
  justify-content: space-between;
  &__img{
    flex-grow: 1;
    max-width: calc(100% - 445px);
    img{
      width: 100%;
    }
  }
  &__content{
    width: 405px;
    flex-shrink: 0;
  }
  &__logo{
    display: block;
    margin: 0 auto;
  }
  &__text{
    font-size: 14px;
    line-height: 19px;
    margin-top: 16px;
    text-align: justify;
  }
  &__btn{
    margin: 16px 0 0 auto;
    width: 125px;
    height: 40px;
  }
  &:nth-child(even){
    flex-direction: row-reverse;
  }
}

.catalog-card{
  display: block;
  &:hover{
    color: #828282;
    .catalog-card{
      &__img{
        &:before{
          opacity: 1;
        }
      }
    }
  }
  &__img{
    position: relative;
    padding-bottom: 100%;
    overflow: hidden;
    &:before{
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: rgba(#5e5e5e,.2);
      opacity: 0;
      z-index: 2;
      transition: all .3s;
    }

    img{
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__title{
    font-size: 16px;
    line-height: 19px;
    margin-top: 16px;
  }
}

.tile-card{
  &__img{
    cursor: pointer;
    img{
      width: 100%;
    }
  }
  &__face{
    cursor: pointer;
    font-size: 14px;
    line-height: 19px;
    text-decoration-line: underline;
    &:hover{
      text-decoration: underline;
      color: #828282;
    }
  }
  &__props{
    display: grid;
    grid-template-columns: 100%;
    gap: 5px;
    margin-top: 32px;
  }
  &__prop{
    display: grid;
    grid-template-columns: 115px auto;
    gap: 6px;
    font-size: 14px;
    line-height: 19px;
    &-desc{
      font-weight: 500;
    }
  }
  &__info{
    margin-top: 32px;
    display: grid;
    grid-template-columns: 100%;
    gap: 1px;
    background: #E5E5E5;
    .tile-card__props{
      margin: 0;
    }
    .tile-card__prop{
      grid-template-columns: 1fr 1fr;
    }
  }
}

.about-card{
  display: flex;
  height: 308px;
  background: #F4F4F4;
  overflow: hidden;
  &__img{
    width: 308px;
    height: 100%;
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__content{
    padding: 40px;
    width: calc(100% - 308px);
  }
  &__title,
  &__desc{
    font-size: 14px;
    line-height: 19px;
    letter-spacing: 0.05em;
  }
  &__title{
    font-weight: 500;
    text-transform: uppercase;
  }
  &__desc{
    text-align: justify;
    margin-top: 20px;
    p{
      line-height: inherit;
      &:not(:last-child){
        margin-bottom: 20px;
      }
    }
    a{
      text-decoration: underline;
      &:hover{
        text-decoration: underline;
        color: #828282;
      }
    }
  }
  &:nth-child(even){
    flex-direction: row-reverse;
  }
}


@media screen and (max-width: 1100px){
  .brand-card{
    &__img{
      max-width: calc(100% - 402px);
      &-wrap{
        position: relative;
        padding-bottom: 100%;
      }
      img{
        position: absolute;
        width: 100%;
        height: 100%;
        object-fit: cover;
        top: 0;
        left: 0;
      }
    }
    &__content{
      width: 378px;
    }
  }
}


@media screen and (max-width: 992px){
  .catalog-card{
    &__title{
      margin-top: 10px;
    }
  }

  .about-card{
    padding: 24px;
    overflow: unset;
    height: unset;
    &__img{
      display: none;
    }
    &__content{
      padding: 0;
      width: 100%;
    }
  }
}


@media screen and (max-width: 767px){
  .brand-card{
    display: block;
    &__img{
      max-width: 100%;
      width: 100%;
    }
    &__content{
      width: 100%;
      margin-top: 16px;
    }
  }

  .about-card{
    padding: 16px;
    &__desc{
      margin-top: 15px;
      p{
        &:not(:last-child){
          margin-bottom: 15px;
        }
      }
    }
  }
}
