.collection{
  &__grid{
    margin-top: 32px;
    display: grid;
    grid-template-columns: calc(100% - 465px) 425px;
    gap: 40px;
  }
  &__content{
    padding-top: 40px;
  }
  &__title{
    font-weight: 300;
    font-size: 24px;
    line-height: 117%;
    letter-spacing: 0.05em;
    text-transform: uppercase;
  }
  &__subtitle{
    font-size: 14px;
    line-height: 19px;
    margin-top: 20px;
    b{
      font-weight: 500;
    }
  }
  &__desc{
    font-size: 12px;
    line-height: 20px;
    margin-top: 20px;
    text-align: justify;
  }
  &__info{
    margin-top: 40px;
    max-width: 425px;
  }
}

.gallery{
  overflow: hidden;
  .swiper{
    position: relative;
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      height: auto;
      position: relative;
      img{
        width: 100%;
        height: 100%;
        display: block;
        object-fit: cover;
      }
    }
    &-button{
      background: rgba(#FFFFFF,.7);
      width: 24px;
      height: 24px;
      top: 50%;
      transform: translateY(-50%);
      margin-top: 0;
      &-prev{
        left: 15px;
      }
      &-next{
        right: 15px;
      }
    }
    &-pagination{
      display: none;
    }
  }
  &__big{
    .play-btn{
      svg{
        width: 72px;
        height: auto;
      }
    }
  }
  &__small{
    margin-top: 40px;
    .swiper{
      &-slide{
        cursor: pointer;
        img{
          opacity: .5;
        }
        &-thumb-active{
          img{
            opacity: 1;
          }
        }
      }
    }
  }
}

.play-btn{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
}

.carousel__slide.has-thumb{
  opacity: .5;
  &.has-html5video{
    &:after{
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-image: url(../img/play.svg);
      background-position: center;
      background-repeat: no-repeat;
    }
  }
  &.is-nav-selected{
    opacity: 1;
  }

}

.fancybox__thumb{
  border-radius: 0;
  &:after{
    display: none;
  }
}

._white-fancy{
  .fancybox__backdrop{
    background-color: #fff;
  }
  .carousel__button{
    color: $black;
  }
  .fancybox__thumbs{
    //position: fixed;
    //top: 0;
    //right: 0;
    //width: 130px;
    //height: 100vh;
    //.carousel__track{
    //  flex-direction: column;
    //}
  }
}

.tile{
  padding: 40px 0 75px;
  &__grid{
    display: grid;
    grid-template-columns: repeat(3,1fr);
    gap: 40px 43px;
  }
}

.tile-modal{
  position: fixed;
  background: #FFFFFF;
  top: 0;
  left: 0;
  z-index: 10000;
  width: 100%;
  height: 100vh;
  padding: 190px 60px;
  opacity: 0;
  pointer-events: none;
  transition: all .3s;
  &._active{
    opacity: 1;
    pointer-events: unset;
  }
  &__close{
    position: absolute;
    top: 60px;
    right: 60px;
  }
  .swiper-pagination{
    position: absolute;
    left: 0;
    bottom: 100%;
    margin-bottom: 10px;
    width: unset;
    font-size: 14px;
    line-height: 19px;
  }
}

.tile-gallery{
  width: 1320px;
  margin: 0 auto;
  max-width: 100%;
  padding-right: 204px;
  height: 100%;
  position: relative;
  .swiper{
    height: 100%;
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      height: auto;
      img{
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }

    &-button{
      &-prev{
        left: -36px;
      }
      &-next{
        right: -36px;
      }
    }
  }
  &__big{
    position: relative;
    height: 100%;
  }
  &__small{
    position: absolute;
    right: 0;
    top: 50%;
    height: 100%;
    max-height: 750px;
    width: 130px;
    transform: translateY(-50%);
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 20px 0;
    .swiper{
      max-height: 350px;
      &-button{
        &-prev,
        &-next{
          left: 50%;
          transform: translateX(-50%);
          margin: 0;
        }
        &-prev{
          top: -36px;
        }
        &-next{
          top: unset;
          bottom: -36px;
        }
      }
    }
  }
}


@media screen and (max-width: 1200px){
  .collection{
    &__grid{
      grid-template-columns: 100%;
      margin: 0;
    }
    &__content{
      padding: 0;
    }
    &__info{
      margin-top: 20px;
    }
  }

  .tile{
    &__grid{
      grid-template-columns: repeat(2,1fr);
    }
  }
}


@media screen and (max-width: 992px){
  .tile{
    padding: 64px 0 40px;
    &__grid{
      grid-template-columns: 100%;
    }
    .tile-card{
      max-width: 425px;
    }
  }

  .tile-modal{
    padding: 60px 6px;
    &__close{
      top: 20px;
      right: 6px;
      svg{
        width: 16px;
        height: auto;
      }
    }
  }

  .tile-gallery{
    padding: 0;
    &__small{
      display: none;
    }
    .swiper{
      &-pagination{
        display: none;
      }
      &-button{
        width: unset;
        svg{
          width: 12px;
          height: auto;
        }
        &-prev{
          left: 18px;
        }
        &-next{
          right: 18px;
        }
      }
    }
  }
}


@media screen and (max-width: 767px){
  .collection{
    &__grid{
      gap: 16px;
    }
    &__info{
      max-width: 100%;
    }
  }

  .gallery{
    .swiper{
      &-pagination{
        position: static;
        margin-top: 16px;
        display: flex;
      }
    }
    &__small{
      display: none;
    }
  }

  .tile{
    padding-top: 48px;
  }
}
