.footer{
  padding: 40px 0;
  border-top: 1px solid #E5E5E5;
  &__inner{
    padding-right: 62px;
    position: relative;
  }
  &__c,
  &__desc,
  &__conf{
    font-size: 12px;
    line-height: 16px;
  }
  &__c{
    font-weight: 500;
  }
  &__conf{
    text-decoration: underline;
    &:hover{
      color: #828282;
      text-decoration: underline;
    }
  }
  &__tg{
    position: absolute;
    right: 0;
    top: 0;
  }
}


@media screen and (max-width: 992px){
  .footer{
    padding: 24px 0;
  }
}


@media screen and (max-width: 767px){
  .footer{
    padding: 16px 0;
    &__inner{
      padding-right: 26px;
    }
  }
}
